/////////////////////////////////////////////
/////////////////////////////////////////////
body {
  position: relative;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
}

html {
  font-size: 62.5%;
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
h4,
h5,
h6,
a,
p {
  color: #102a42;
}

/*  fluid typography   */
h1 {
  font-size: clamp(3rem, 4.5rem, 6rem);
  padding: 1rem 0;
  font-weight: 700;
}

h5 {
  font-size: clamp(1rem, 1.5rem, 2rem);
  color: rgba(0, 128, 128, 0.729);
}

/*  fluid typography   */

*,
*::after,
*::before {
  box-sizing: border-box;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 70%;
    height: 100%;
    background: #e0fcff;
    z-index: -2;
  }

  div {
    padding: 2rem;
    margin-left: 17.5%;
    margin-top: 2.5rem;
  }
  nav {
    padding: 2rem;
    margin-right: 17.5%;
    margin-top: 2.5rem;

    a {
      margin-inline: 1rem;
      text-decoration: none;
      color: black;
      font-size: 2rem;
      padding-bottom: 1rem;
      font-weight: 600;

      &:active {
        border-bottom: 3px solid teal;
      }

      &:hover {
        color: teal;
        border-bottom: 3px solid teal;
      }
    }
  }
}

main {
  width: 100%;
  min-height: 100vh;
  // padding: 5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  flex-grow: 1;
  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 70%;
    height: 100vh;
    background: #e0fcff;
    z-index: -2;
  }

  section {
    &:first-child {
      width: clamp(35%, 50%, 600px);

      h1 {
        // font-size: clamp(3rem, 4.5rem, 6rem);
        padding: 1rem 0;
        font-weight: 700;
        span {
          margin-right: 1rem;
          border-top: 1rem teal solid;
        }
      }

      h5 {
        //   font-size: clamp(1rem, 1.5rem, 2rem);
        color: rgba(0, 128, 128, 0.729);
      }

      button {
        margin: 2rem 0;
        background: teal;
        color: whitesmoke;
        transition: 0.3s;
        border: none;
        padding: 1.5rem;
        font-weight: 700;
        border-radius: 5px;

        &:hover {
          background: rgba(0, 128, 128, 0.5);
          color: rgb(2, 40, 40);
        }
      }
    }

    &:nth-of-type(2) {
      padding: 1.8rem;
      background: #1a2026;
      border-radius: 15px;
      max-width: 900px;
      p {
        color: white;
        font-size: 2rem;
        padding: 1.5rem;
      }

      span {
        color: #e2410a;
        margin: 0.25rem;
      }
    }
  }
}

/*  Projects Section   */
#Projects {
  background: #f1f5f8;
  display: flex;
  flex-direction: column;
  padding: 2rem;
  justify-content: center;
  align-items: center;
  h1 {
    font-size: 4rem;
    font-weight: 700;
    margin-bottom: 5rem;
  }

  .project {
    width: 50%;
    background: rgba(255, 255, 255, 0.708);
    margin: 1rem 0;
    padding: 5rem;
    display: flex;
    flex-direction: column;
    position: relative;
    gap: 1rem;
    transition: 0.5s;
    border-radius: 10px;

    h2 {
      color: #352a42;
      font-size: 2.5rem;
    }
    p {
      line-height: 1.5;
      font-size: 1.5rem;
      margin: 1.5rem 0;
    }

    &:nth-of-type(odd) {
      margin-left: 30%;
    }
    &:nth-of-type(even) {
      margin-right: 30%;
    }
    .stack {
      margin-bottom: 1rem;
      display: flex;
      flex-wrap: wrap;
      span {
        background: #dae2ec;
        margin: 0.5rem;
        padding: 0.5rem 1rem;
        color: #617d98;
      }
    }
    .position {
      color: rgb(94, 169, 163);
      font-size: 2rem;
      position: absolute;
      top: 0;
      left: 0;
      padding: 1rem;
    }
  }
  .buttons {
    display: flex;
    gap: 1rem;

    button {
      background: teal;
      border: none;
      padding: 0.5rem 1.5rem;
      border-radius: 4px;
      transition: 0.3s;
      &:hover {
        background: rgba(0, 128, 128, 0.626);
      }
      a {
        text-decoration: none;
        color: white;
      }
    }
  }
}
#Contact {
  background: teal;
  text-align: center;
  padding: 5rem;
  min-height: 30vh;
  display: grid;

  h1 {
    font-size: 3rem;
    color: white;
    padding: 1rem;
  }

  .contact-icons {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5rem;

    div {
      padding: 1rem;

      > * {
        margin: 0.5rem;
        color: white;
      }

      > h4 {
        margin: 0.5rem;
        font-size: 1.5rem;
        color: cornsilk;
      }
    }
  }
}

@media screen and (max-width: 800px) {
  main {
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-start;

    section {
      &:first-child {
        padding: 0;
      }
    }
  }

  #Projects {
    .project {
      width: 65%;
    }
  }
}

@media screen and (max-width: 600px) {
  .header {
    padding: 4rem;
    margin: 0;

    div {
      padding: 0;
      margin: 0;
    }

    nav {
      padding: 0;
      margin: 0;
    }
  }

  main {
    flex-direction: column;
    padding-top: 0rem;
    padding: 1rem;
    section {
      &:first-child {
        padding: 2rem;
        margin: 0;
        width: 100%;
      }
      &:nth-of-type(2) {
        padding: 1.8rem;
        background: #1a2026;
        border-radius: 15px;
        width: 100%;
        p {
          color: white;
          font-size: 2rem;
          padding: 1.5rem;
        }

        span {
          color: #e2410a;
          margin: 0.25rem;
        }
      }
    }
  }

  #Projects {
    justify-content: center;
    align-items: center;
    h1 {
      text-align: center;
      line-height: 1.6;
    }

    .project {
      width: 100%;
      background: rgba(255, 255, 255, 0.708);
      margin: 1rem 0;
      padding: 5rem;
      &:nth-of-type(odd) {
        margin: 0;
        margin-top: 1rem;
      }
      &:nth-of-type(even) {
        margin: 0;
        margin-top: 1rem;
      }
    }
  }

  #Contact {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}

@media screen and (max-width: 400px) {
  .header {
    padding: 1rem;
  }

  main {
    display: flex;
    padding-top: 10rem;
    justify-content: space-between;
    gap: 10rem;
    section {
      margin: 0;
      padding: 0;
    }
  }

  #Projects {
    margin: 0;
    padding: 0;
    width: 100vw;

    h1 {
      font-size: 3.5rem;
      line-height: 1.6;
      margin: 0;
    }
  }

  #Contact {
    div {
      padding: 1rem;
      margin-right: 1rem;
    }

    h1 {
      font-size: 2rem;
      margin: 0;
    }
  }
}
